import React from 'react';

import Card from '../components/Card';

import github from '../images/github.png';
import alan from '../images/alan.jpg';
import pigeon from '../images/pigeon.jpg';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';

class Carousel extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            items: [
                {
                    id: 0,
                    title: 'GitHub Repo',
                    subTitle: 'My open source contributions',
                    imgSrc: github,
                    link: 'https://github.com/a1764879',
                    selected: false
                },
                {
                    id: 1,
                    title: 'Alan Pallath',
                    subTitle: 'LinkedIn',
                    imgSrc: alan,
                    link: 'https://www.linkedin.com/in/alanpallath',
                    selected: false
                },
                {
                    id: 2,
                    title: 'Behance',
                    subTitle: 'Photos and designs',
                    imgSrc: pigeon,
                    link: 'https://www.behance.net/iamakidilam',
                    selected: false
                },
            ]
        }
    }


    handleCardClick = (id, card) => {

        let items = [...this.state.items];

        items[id].selected = items[id].selected ? false : true;

        items.forEach(item => {
            if(item.id !== id) {
                item.selected = false;
            }
        });

        this.setState({
            items
        });
    }


    makeItems = (items) => {
        return items.map(item => {
            return <Card item={item} click={(e => this.handleCardClick(item.id, e))} key={item.id} />
        })
    }


    render() {
        return(
            <Container fluid={true}>
                <Row className="justify-content-around">
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        );
    }

}

export default Carousel;